@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
}
img {
  width: 100%;
  max-width: 300px;
  height: auto;
}

.slick_pic img {
  width: 100%;
  max-width: 100%;
  height: auto;
}
.slick_pic {
  max-height: 585px;
  overflow: hidden;
}
.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 50px;
  line-height: 1;
  opacity: 0.75;
  color: #170b0b;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-next {
  right: 50px;
}
.slick-prev {
  left: 50px;
}
.slick-prev,
.slick-next {
  z-index: 99999999;
}

