.btn_line {
  position: relative;
  display: inline-block;
  overflow: hidden;
  padding: 25px 25px;
  margin-top: 5px;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-decoration: none;
  color: #000;
  transition: color 0.125s 0.75s;
}
.btn_line:before,
.btn_line:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: border-color 0s 0.5s linear, -webkit-transform 1s linear;
  transition: transform 1s linear, border-color 0s 0.5s linear;
  transition: transform 1s linear, border-color 0s 0.5s linear, -webkit-transform 1s linear;
}
.btn_line:before {
  border-left: 2px solid transparent;
  border-right: 2px solid transparent;
}
.btn_line:after {
  border-top: 2px solid transparent;
  border-bottom: 2px solid transparent;
}
.btn_line:hover {
  color: #000;
}
.btn_line:hover:before,
.btn_line:hover:after {
  border-color: rgb(53, 3, 170);
}
.btn_line:hover:before {
  -webkit-transform: skewY(180deg);
          transform: skewY(180deg);
}
.btn_line:hover:after {
  -webkit-transform: skewX(-180deg);
          transform: skewX(-180deg);
}

