  /* aspect-ratio: 600/400; */
  /* border: 1px solid red; */
  padding: 0px;
}
.sml_bnr img {
  width: 100%;
  max-width: inherit;
  /* height: 100%; */
}

.product h3 {
  font-size: 20px;
  font-weight: 400;
}
.product img {
  width: 80%;
  margin-top: 1rem;
  margin-left: auto;
  margin-right: auto;
}
.product .details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.product .details .price {
  font-size: 20px;
  font-weight: 700;
}
.product button {
  width: 100%;
